const Components = {
	Layer: (resolve) => require(['./pages/layer'], resolve),
	Asset: (resolve) => require(['./pages/asset'], resolve)
}

export default {
	path: '/detail',
	name: 'detail_page',
	component: Components.Layer,
	children: [
		{
			path: 'asset/:id',
			name: 'asset_page',
			component: Components.Asset
		}
	]
}


